import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileExportScriptListResponse } from "../../Model/Response/FileExportScriptListResponse";
import { FileExportScriptResponse } from "../../Model/Response/FileExportScriptResponse";
import { FileExportScriptViewModel } from '../../Model/ViewModel/FileExportScriptViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileExportScriptServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileExportScriptServicesProvider extends FileServicesProvider {
  private FileExportScripts: FileExportScriptListResponse;
  private FileExportScript: FileExportScriptResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileExportScript";
    console.log("Hello FileExportScriptServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileExportScript?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileExportScript/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileExportScripts = new FileExportScriptListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileExportScripts;
    });
  }



  public PostPutAsync(FileExportScriptViewModel:FileExportScriptViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileExportScript";
    else
    sub_url = "FileExportScript/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileExportScriptViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileExportScriptViewModel,mode).map(
      item => {
        this.FileExportScript = new FileExportScriptResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileExportScript;
      }
    );
  }
}



