import { SingleModelResponse } from "./SingleModelResponse";
import { FileExportScriptViewModel } from "../ViewModel/FileExportScriptViewModel";

export class FileExportScriptResponse extends SingleModelResponse<
  FileExportScriptViewModel
> {
  Model: FileExportScriptViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new FileExportScriptViewModel();
      this.Model.exec_file_seq = obj.Model.exec_file_seq;
      this.Model.exec_group = obj.Model.exec_group;
      this.Model.is_active = obj.Model.is_active;
      this.Model.exec_seq = obj.Model.exec_seq;
      this.Model.can_rerun = obj.Model.can_rerun;
      this.Model.parameter_column_group = obj.Model.parameter_column_group;
      this.Model.creator = obj.Model.creator;
      this.Model.create_time = obj.Model.create_time;
      this.Model.modifier = obj.Model.modifier;
      this.Model.last_update_time = obj.Model.last_update_time;
      this.Model.note = (obj && obj.Model.note) || null;
      this.Model.is_active_desc = obj.Model.is_active_desc;
      switch (obj.Model.is_active) {
        case false: {
          this.Model.is_active_color = "danger";
          break;
        }

        default:
          this.Model.is_active_color = "active";
      }
      this.Model.out_file_path_key = obj.Model.out_file_path_key;
      this.Model.out_file_path = obj.Model.out_file_path;
      this.Model.sql_file_path_key=obj.Model.sql_file_path_key;
      this.Model.sql_file_path=obj.Model.sql_file_path;
      this.Model.bk_file_path_key=obj.Model.bk_file_path_key;
      this.Model.bk_file_path=obj.Model.bk_file_path;
      this.Model.schemaname = obj.Model.schemaname;
      this.Model.tablename = obj.Model.tablename;
      this.Model.tablefullname = obj.Model.tablefullname;
      this.Model.db_conn_id = obj.Model.db_conn_id;
      this.Model.db_conn_string = obj.Model.db_conn_string;
      this.Model.parameter_column_group = obj.Model.parameter_column_group;
      this.Model.filename = obj.Model.filename;
      this.Model.script_filename = obj.Model.script_filename;
      this.Model.script_file_proc_key = obj.Model.script_file_proc_key;
      this.Model.script_file_proc = obj.Model.script_file_proc;
      this.Model.file_format_key = (obj && obj.Model.file_format_key) || null;
      this.Model.file_format = (obj && obj.Model.file_format) || null;
      this.Model.field_terminator = (obj && obj.Model.field_terminator) || null;
      this.Model.row_terminator = (obj && obj.Model.row_terminator) || null;
      this.Model.src_export_type_key = obj.Model.src_export_type_key;
      this.Model.src_export_type = obj.Model.src_export_type;
      this.Model.is_processed_exec_group=obj.Model.is_processed_exec_group;
      this.Model.is_fail_stop=obj.Model.is_fail_stop;
      this.Model.export_mode_key=obj && obj.Model.export_mode_key || null;
      this.Model.last_data_type_key=obj && obj.Model.last_data_type_key || null;
      this.Model.last_data_column_name=obj && obj.Model.last_data_column_name || null;
      this.Model.where_condition=obj && obj.Model.where_condition || null;
      this.Model.del_empty_file= obj.Model.del_empty_file;
      if(this.Model.is_processed_exec_group===true)
      {
        this.Model.processed_exec_group_color="danger";
      }
      else
      this.Model.processed_exec_group_color="light";
    }
  }
}



